.show {
  width: 100%;
  height: 20rem;

  .edit {
    visibility: hidden;
    height: 0;
  }

  .photo {
    width: 100%;
    height: 20rem;
    background-color: var(--colorWhite);
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    object-fit: cover;
  }

  .circle {
    visibility: hidden;

    .delete {
      visibility: hidden;
      height: 0;
    }
  }


}

.editing {
  @extend .photo;
  width: 100%;
  height: 20rem;
  display: inline-grid;
  grid-template-columns: 10% 80% 10%;
  grid-template-rows: auto;
  grid-template-areas: "one", "two", "three";



  .edit {
    grid-area: "one";
    width: 2rem;
    padding: 1rem;
    height: 2rem;
    position: relative;
    bottom: 1rem;
  }

  .photo {
    grid-area: "two";
    width: 100%;
    height: 20rem;
  }

  .circle {
    grid-area: "three";
    position: relative;
    right: 2.2rem;
    bottom: 2rem;
    border-radius: 50%;
    box-shadow: var(--boxShadow);
    background-color: var(--colorWhite);
    height: 5rem;
    width: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;

    .delete {
      width: 2rem;
      padding: 1rem;
      height: 2rem;
    }
  }


}