.show {
  width: 100%;
  height: fit-content;
  display: inline-grid;
  grid-template-columns: 10% 80% 10%;
  grid-template-rows: auto;

  .edit {
    display: none;
  }

  .link {
    align-self: center;
    grid-column-start: 1;
    grid-row-start: 1;
    grid-column-end: 2;
    grid-row-end: 2;
    width: 2rem;
    padding: 1rem;
    height: 2rem;
    color: red;
  }

  .text {
    font: var(--textFormMessage);
    align-self: center;
    margin: 0 .5rem;
    grid-column-start: 2;
    grid-row-start: 1;
    grid-column-end: 3;
    grid-row-end: 2;
  }

  .delete {
    display: none;
  }

}

.editing {
  @extend .show;

  .edit {
    display:block;
    align-self: center;
    grid-column-start: 1;
    grid-row-start: 1;
    grid-column-end: 2;
    grid-row-end: 2;
    visibility: visible;
    width: 2rem;
    padding: 1rem;
    height: 2rem;
    color: red;
  }

  .text {
    align-self: center;
    margin: 0 .5rem;
    grid-column-start: 2;
    grid-row-start: 1;
    grid-column-end: 3;
    grid-row-end: 2;

  }

  .delete {
    display:block;
    align-self: center;
    visibility: visible;
    grid-column-start: 3;
    grid-row-start: 1;
    grid-column-end: 4;
    grid-row-end: 2;
    width: 2rem;
    padding: 1rem;
    height: 2rem;
  }

  .link {
    display: none;
  }

}